<template>
  <div>
    <nav class="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
      <a class="navbar-brand font-weight-bold col-sm-3 col-md-2 mr-0 text-white">Patients List</a>
      <ul class="navbar-nav px-3">
        <li class="nav-item text-nowrap">
          <router-link to="/" class="nav-link">Log Out</router-link>
        </li>
      </ul>
    </nav>
    <div class="container min-vh-100 vw-100 col-12 mt-5">
      <div
        class="d-flex col-12 col-lg-10 justify-content-around
          flex-wrap align-items-center mx-auto w-100" v-if="patients && fakePatientInfo"
      >
        <div
          class="card my-5 c-card-setting c-card-animation"
          v-for="(info, key) in patients"
          :key="key"
        >
          <div class="d-flex justify-content-center">
            <img src="../assets/images/male_icon-01.png" class="card-img-top w-75" />
          </div>
          <div class="card-body">
            <h3 class="card-text font-weight-bold">Patient {{ key + 1 }}</h3>
          </div>
          <ul class="list-group list-group-flush">
            <li class="list-group-item">Age: {{fakePatientInfo.dob.age}}</li>
            <li class="list-group-item">Gender: {{fakePatientInfo.gender}}</li>
            <li class="list-group-item">
              userID:
              <span class="text-danger">{{info.value}}</span>
            </li>
            <li class="list-group-item">
              <p class="m-0" :class = "{'text-success':patientOnBedData[0] === 1}">
                <span class="c-patient-signal" id="userID"
                  :class="{'bg-success':patientOnBedData[0] === 1}">
                </span>
                Patient on bed
              </p>
            </li>
            <!-- <div class="list-group-item"> -->
              <!-- <div class="col-lg-8 col-12 mx-auto"> -->
                <!-- <canvas class="my-4 w-100" id="breathe-chart"></canvas> -->
              <!-- </div> -->
            <!-- </div> -->
            <li class="list-group-item text-center bg-primary
              text-white h5 font-weight-bold c-list-select"
              @click="enterDetail(info.value)">Select</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  data() {
    return {
      admin: {
        // adminId: null,
        adminView: null,
      },
      patientOnBedData: [],
      fakePatientInfo: null,
      patientGroup: {
        all: [],
        group1: [],
        group2: [
          { value: 'fake1' },
          { value: 'fake2' },
          { value: 'fake3' },
          { value: 'fake4' },
          { value: 'fake5' },
          { value: 'fake6' },
        ],
      },
      patients: null,
      chart: {
        xAxis: [],
        datas: [],
      },
      fullDate: [],
      fullTime: [],
      fullValue: [],
    };
  },
  methods: {
    getPatientApi(userID = 9999) {
      const vm = this;
      const cors = 'https://cors-anywhere.herokuapp.com/';
      // const url = 'http://52.15.225.56/api/v1/series/bedStatus?startTimestamp=1566864000&endTimestamp=1566950400&userID=9999'; // default
      // const url = `http://52.15.225.56/api/v1/series/bedStatus?startTimestamp=${ // realTime
      //   Math.floor((new Date().getTime()) / 1000)
      //   }&endTimestamp=${
      //   Math.floor((new Date().getTime()) / 1000)
      //   }&userID=${userID}`;
      const url = `http://52.15.225.56/api/v1/series/bedStatus?startTimestamp=1567403940&endTimestamp=1567404000&userID=${15}`; // testTime
      const xhr = new XMLHttpRequest();
      const auth = JSON.parse(localStorage.getItem('token')).token;
      xhr.open('get', cors + url);
      xhr.setRequestHeader('Authorization', `Bearer ${auth}`);
      xhr.send(auth);
      xhr.onloadend = () => {
        if (!JSON.parse(xhr.response).data) {
          // eslint-disable-next-line
          alert('API ERROR');
          return;
        }
        JSON.parse(xhr.response).data.forEach((el) => {
          vm.patientOnBedData.push(el.value);
        });
        // eslint-disable-next-line
      };
    },
    setRealTime() {
      this.patientOnBedData.splice(0, 1);
    },
    enterDetail(userID) {
      const vm = this;
      localStorage.setItem('userID', JSON.stringify({ userID: userID }));
      vm.$store.dispatch('loadingOverlay');
      setTimeout(() => {
        vm.$store.dispatch('loadingOverlay');
        vm.$router.push({ path: 'detail' });
      }, 750);
    },
    getFakePatients() { // 抓假資料
      const vm = this;
      const url = 'https://randomuser.me/api/';
      const xhr = new XMLHttpRequest();
      let result;
      vm.$store.dispatch('loadingOverlay');
      xhr.open('get', url);
      xhr.send(null);
      xhr.onloadend = () => {
        result = JSON.parse(xhr.response);
        vm.fakePatientInfo = { ...result.results[0] };
        vm.$store.dispatch('loadingOverlay');
      };
    },
    getPatinetsInfo() { // 藉由 auth 取得資料
      const vm = this;
      const cors = 'https://cors-anywhere.herokuapp.com/';
      const url = 'http://52.15.225.56/api/v1/series/userID';
      const xhr = new XMLHttpRequest();
      const auth = JSON.parse(localStorage.getItem('token')).token;
      const admin = JSON.parse(localStorage.getItem('admin')).admin;
      let tempAry = [];
      const apiError = setTimeout(() => {
        // eslint-disable-next-line
        alert('API Error');
      }, 5000);
      xhr.open('get', cors + url);
      xhr.setRequestHeader('Authorization', `Bearer ${auth}`);
      xhr.send(auth);
      xhr.onloadend = () => {
        clearTimeout(apiError);
        tempAry = JSON.parse(xhr.response).data;
        if (admin === 'admin1') {
          vm.patientGroup.group1 = tempAry.filter((el) => {
            return el.value < 10;
          });
        } else {
          vm.patientGroup.group1 = tempAry.filter((el) => {
            return el.value < 20 && el.value >= 11;
          });
        }
        vm.getFakePatients();
        vm.patientGroup.all = vm.patientGroup.group1.concat(vm.patientGroup.group2);
        vm.setAdminView();
      };
    },
    getAdminInfo() { // 取得userID
      const vm = this;
      // vm.admin.adminId = JSON.parse(localStorage.getItem('adminId')).adminId;
      vm.admin.adminView = JSON.parse(localStorage.getItem('adminView')).adminView;
    },
    setAdminView() { // 根據 localStorage 的假資料判斷顯示數量
      const vm = this;
      if (vm.admin.adminView === 'group1') {
        vm.patients = [...vm.patientGroup.group1];
      } else if (vm.admin.adminView === 'group2') {
        vm.patients = [...vm.patientGroup.group2];
      } else if (vm.admin.adminView === 'all') {
        vm.patients = [...vm.patientGroup.all];
      }
    },
  },
  created() {
    this.getPatinetsInfo();
    this.getAdminInfo(); // 取得假資料
    this.getPatientApi();
    // this.getRamdomPeople2();
  },
  mounted() {
    const reSetRealTime = setInterval(() => this.setRealTime(), 1000);
    const reGetRealTime = setInterval(() => this.getPatientApi(), 60000);
  },
};
</script>
